require("./bootstrap");

$(".telefone").mask("(99) 99999-9999");

$.fn.select2.defaults.set("theme", "bootstrap-5");
$("select.select2").select2();

$("input.value.negative").mask("000.000,00", {
    reverse: true,
    translation: {
        0: {
            pattern: /-|\d/,
            recursive: true,
        },
    },
    onChange: function (value, e) {
        e.target.value = value
            .replace(/^-\./, "-")
            .replace(/^-,/, "-")
            .replace(/(?!^)-/g, "");
    },
});

