$("input.select2").each(function (_, el) {
    $(el)
        .select2({
            placeholder: "",
            minimumInputLength: 2,
            multiple: false,
            quietMillis: 500,
            ajax: {
                url: $(el).data("route"),
                dataType: "json",
                type: "GET",
                data: function (params) {
                    return {
                        search: params.term, // search term
                    };
                },
            },
        })
        .on("select2:select", function (e) {
            let data = e.params.data;
            let ret = null;
            if (data.selected == true) {
                $(el).val(data.text);
                $(el)
                    .parent()
                    .find(".select2-selection__placeholder")
                    .text(data.text);
                if ((ref = $(el).data("ref")) !== undefined) {
                    if ((ret = $(`#${ref}`)).length) {
                        ret.val(data.id);
                    }
                }
            }
        });

    $(el).parent().find(".select2-selection__placeholder").text($(this).val());
});

$("body").on("click", ".btn-link-delete", function (event) {
    const el = $(this);
    event.preventDefault();

    Swal.fire({
        title: "Voce tem certeza?",
        text: $(el).parent().find("form").data("text"),
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Sim",
        cancelButtonText: "Cancelar",
    }).then((result) => {
        if (result.isConfirmed) {
            $(el).parent().find("form").submit();
        }
    });
});
